<template>
  <v-container fluid pa-0>
    <v-row no-gutters>
      <ToolBar
      :version="version"
      @setMode="setMode"
      >
      </ToolBar>
      <v-col cols="3" v-for="(container, index) in containerList" v-bind:key="index">
        <Panel 
          :container="container.value"
          :mode="mode"
          @closeQuestion="closeQuestion"
        ></Panel>
      </v-col>
    </v-row>
    <AskQuestion
    :question="question"
    :containerName="containerName"
    ></AskQuestion>
  </v-container>
</template>

<script>
import { KunlunApi, hostname } from '../api'
import store from '../store'
import Panel from './Panel'
import ToolBar from '../ToolBar'
import AskQuestion from './AskQuestion'

export default {
  name: 'ContainernPage',
  components: {
    ToolBar,
    Panel,
    AskQuestion
  },
  data: () => ({
    url: '',
    ws: null,
    containerList: [],
    station: '',
    containerName: '',
    version: '',
    mode: 'PRODUCT',
    question: {
      name: '',
      type: 'question',
      question: '',
      options: [],
      multiple: false,
      visible: true,
      image: ''
    }
  }),
  mounted () {
    this.url = window.location.hash.substring(1)  // "/kunlun/pcbst"
    this.station = this.url.split('/')[2].toUpperCase()
    this.initSocket()
  },
  beforeDestroy () {
    this.ws.close()
  },
  methods: {
    initSocket () {
      // ws = "ws://localhost:8000/ws/kunlun/pcbst"
      this.getPlatformVersion()
      const url = hostname.replace('http', 'ws') + '/ws' + this.url
      this.ws = new WebSocket(url)
      this.ws.onmessage = this.onMessageSocket
      this.ws.onerror = this.onErrorSocket
      this.ws.onclose = this.onCloseSocket
    },
    onErrorSocket () {
      store.commit('setMessage', 'WS Connection Lost')
      setTimeout(() => {
        this.initSocket()
      }, 3000)
    },
    onSendSocket (data) {
      this.ws.send(JSON.stringify(data))
    },
    onCloseSocket () {
      return false
    },
    onMessageSocket (e) {
      const data = JSON.parse(e.data)
      // console.log(data)
      if (data.type === 'container') {       
        this.containerList.forEach(function (item) {
            if (item.name === data.name) {
              item.value = data.data
            }
        })
      }
      if (data.type === 'question') {
        this.question = data.data
        this.containerName = data.name
        // console.log(this.question)
      }
    },
    getContainerList () {
      KunlunApi({ service: 'get-all-containers', station: this.station })
        .then(response => {
          this.containerList = response.data.data
          // console.log(this.containerList)
        })
        .catch(e => {
          console.log(e)
        })
    },
    closeQuestion (name) {
      if (name === this.containerName) { this.containerName = '' }
    },
    getPlatformVersion () {
      KunlunApi({ service: 'get-platform-version' })
        .then(response => {
          this.version = response.data.data
          this.getContainerList()
        })
        .catch(e => {
          console.log(e)
        })
    },
    setMode (mode) {
      this.mode = mode
    }
  }
}
</script>
