<template>
  <v-container fluid pa-1 style="padding-bottom: 0px;">
    <v-hover v-slot="{ hover }">
      <v-card
        :elevation="hover ? 5 : 3"
        :class="{ 'on-hover': hover }"
        :color="containerColor"
        height="175px"
      >
        <v-card-title>
          <v-row no-gutters>
          <v-col cols="10">
            <span>
              <a :href="'#/kunlun/' + container.name.replace(':', '/').toLowerCase()">
                <h3 class="black--text font-weight-light">{{ container.name }}</h3>
              </a>
            </span>
            <span class="black--text body-2" v-show="testTime">{{ hours + ':' + minutes + ':' + seconds }}</span><br>
            <v-divider style="margin-top: -3px;"></v-divider>
            <span class="black--text body-2">{{ container.display1 }}</span><br>
            <v-divider style="margin-top: -3px;"></v-divider>
            <span class="black--text body-2">{{ container.display2 }}</span><br>
            <v-divider style="margin-top: -3px;"></v-divider>
            <span class="black--text body-2">{{ container.display3 }}</span><br>
          </v-col>
          <v-col cols="2">
            <span>
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    icon
                    color="black"
                    v-bind="attrs"
                    v-on="on"
                    @click="containerActionClick"
                  >
                    <v-icon>{{ containerIcon }}</v-icon>
                  </v-btn>
                </template>
                <span>{{ containerAction }}</span>
              </v-tooltip>
              <v-spacer></v-spacer>
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-show="container.status !== 'idle'"
                    color="black"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    {{ modeIcon }}
                  </v-icon>
                </template>
                <span>{{ container.mode }}, by {{ container.starter }}</span>
              </v-tooltip>
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-show="container.status === 'fail'"
                    color="black"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    sentiment_very_dissatisfied
                  </v-icon>
                </template>
                <span>FAIL</span>
              </v-tooltip>
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-show="container.status === 'stop'"
                    color="black"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    mood_bad
                  </v-icon>
                </template>
                <span>Stopped by {{ container.stopper }}</span>
              </v-tooltip>
              <v-tooltip right v-show="container.queue">
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-show="container.queue"
                    color="black"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    screen_lock_portrait
                  </v-icon>
                </template>
                <span>In Queue</span>
              </v-tooltip>
              <v-tooltip right>
                <template v-slot:activator="{ on, attrs }">
                  <v-icon v-show="container.sync"
                    color="black"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    mdi-sync
                  </v-icon>
                </template>
                <span>In Sync Up</span>
              </v-tooltip>
            </span>
            <v-spacer></v-spacer>
          </v-col>
          </v-row>
        </v-card-title>
        <!-- <v-progress-linear style="margin-top: -12px;" value="30"></v-progress-linear> -->
      </v-card>
    </v-hover>
    <v-dialog
      max-width="800"
      v-model="dialog"
    >
      <v-card>
        <v-card-title></v-card-title>
        <v-card-text>
          <h1 class="black--text font-weight-light">{{ container.name }} Are You Sure to Stop Test? </h1>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn
            color="error"
            @click="containerActionClick"
          >Confirm</v-btn>
          <v-btn
            color="primary"
            @click="dialog = false"
          >Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { KunlunApi } from '../api'
import store from '../store'

export default {
  name: 'ContainerPanel',
  props: ['container', 'mode'],

  data: () => ({
    dialog: false,
    timeCounter: null,
    hours: 0,
    minutes: 0,
    seconds: 0,
    counter: 0
  }),
  computed: {
    containerAction () {
      if (this.container.status === 'idle') { return 'Start Test' }
      else if (this.container.status === 'run') { return 'Stop Test' }
      else if (this.container.status === 'stop' || this.container.status === 'fail' || this.container.status === 'pass') { return 'Deposit Test' } 
      else { return 'Start Test' }
    }, 
    containerIcon () {
      if (this.container.status === 'idle') { return 'play_arrow' }
      else if (this.container.status === 'run') { return 'highlight_off' }
      else if (this.container.status === 'stop' || this.container.status === 'fail' || this.container.status === 'pass') { return 'delete_forever' } 
      else { return 'play_arrow' }
    },
    containerColor () {
      if (this.questionContainer === this.container.name && this.container.status === 'run') { return 'info' }
      else if (this.container.status === 'run') { return 'orange' }
      else if (this.container.status === 'idle') { return '' }
      else if (this.container.status === 'fail' || this.container.status === 'stop') { return 'error' } 
      else if (this.container.status === 'pass') { return 'success' }
      else { return 'primary' }
    },
    modeIcon () {
      if (this.container.mode === 'PRODUCT') { return 'stars' }
      if (this.container.mode === 'DEVELOP') { return 'bug_report' } 
      return 'stars'
    },
    testTime () {
      if (this.container.test_time) { return this.container.test_time }
      return ''
    }
  },
  watch: {
    testTime () {
      if (this.testTime && this.container.status === 'run') {
        this.counter = parseInt(this.testTime)
        this.startCount()
      } else {
        this.stopCounter()
      }
    }
  },
  mounted () {
    this.counter = parseInt(this.container.test_time)
    this.calcTimer()
    if (this.testTime && this.container.status === 'run') {
      this.startCount()
    }
  },
  destroyed () {
    this.stopCounter()
  },
  methods: {
    startCount () {
      if (!this.timeCounter) {
        this.counter = 0
        this.timeCounter = setInterval(this.calcTimer, 1000)
      }
    },
    calcTimer () {
      this.seconds = this.timerFormat(++this.counter % 60)
      this.minutes = this.timerFormat(parseInt(this.counter / 60, 10) % 60)
      this.hours = this.timerFormat(parseInt(this.counter / 3600, 10))
    },
    timerFormat (timer) {
      if (timer) {
        return timer > 9 ? timer : '0' + timer
      } else { return '00' }
    },
    stopCounter () {
      clearInterval(this.timeCounter)
      this.timeCounter = null
    },
    containerActionClick () {
      let service = 'deposit-test'
      if (this.container.status === 'idle') { service = 'start-test'}
      if (this.container.status === 'run') { 
        if (this.dialog == false) {
          this.dialog = true
          return false
        }
        this.dialog = false
        service = 'stop-test'
        this.$emit("closeQuestion", this.container.name)
      }
      KunlunApi({ service: service, container: this.container.name, mode: this.mode, username: 'kunlun' })
        .then(response => {
          store.commit('setMessage', response.data.message)
        })
        .catch(e => {
          console.log(e);
        });
    }
  }
}
</script>
<style scoped>
a:link {
    background-color: transparent;
    text-decoration: none;
}
a:hover {
    color: red;
    background-color: transparent;
    text-decoration: underline;
}
</style>